.price-includes {
  position: relative;
  background-color: var(--color-metal);
  @include flex-all-sb;
  margin-bottom: 100px;

  @include small-tablet {
    margin-bottom: 40px;
  }

  &__title {
    padding: 82px 275px 520px 139px;
    width: 66%;
    position: relative;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 40px;
    line-height: 49px;
    color: var(--color-bblue);

    @include big-desktop {
      padding: 20px 280px 455px 40px;
    }

    @include small-tablet {
      font-size: 27px;
      line-height: 36px;
      padding: 20px 100px 340px 10px;
    }

    @include mobile {
      padding-right: 0;
      padding-left: 0;
    }


    &::before {
      content: "";
      right: -96px;
      bottom: 21px;
      width: 100%;
      height: 100%;
      position: absolute;
      background-image: url(../img/barnhouse-of-ourkeys.png);
      background-position: center;
      background-repeat: no-repeat;
      background-size: contain;

      @include small-tablet {
        right: -116px;
        background-size: cover;
      }

      @include mobile {
        right: 15px;
      }
    }

    &::after {
      position: absolute;
      content: "";
      left: 142px;
      top: 220px;
      width: 96px;
      height: 11px;
      background-color: var(--color-primary);

      @include big-desktop {
        top: 167px;
        left: 43px;
      }

      @include small-tablet {
        height: 7px;
        top: 119px;
        left: 13px;
      }

      @include mobile {
        top: 98px;
      }
    }
  }

  &__list {
    margin: 0 150px 0 0;
    display: flex;
    flex-direction: column;

    @include tablet {
      margin-right: 40px;
    }

    @include small-tablet {
      margin-right: 20px;
    }
  }

  &__text {
    position: absolute;
    display: inline-block;
    width: 300px;
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 21px;
    text-align: center;
    color: #5B5B5B;
    transition: opacity 0s;
    opacity: 0;

    @include big-desktop {
      width: 50%;
    }

    &-roof {
      top: 50px;
      left: -400px;

      @include big-desktop {
        top: 90%;
        left: 10%;
      }

      @include small-tablet {
        top: 85%;
        left: 22%;
      }

      @include mobile {
        width: 100%;
        left: 0;
      }
    }

    &-frame {
      top: 50px;
      left: -400px;
      width: 400px;

      @include big-desktop {
        top: 90%;
        left: 10%;
      }

      @include small-tablet {
        top: 85%;
      }

      @include mobile {
        left: 0;
      }
    }


    &-doors {
      top: 50px;
      left: -400px;

      @include big-desktop {
        top: 90%;
        left: 10%;
      }

      @include small-tablet {
        top: 85%;
        left: 25%;
      }
    }

    &-net {
      top: 50px;
      left: -500px;
      width: 500px;

      @include big-desktop {
        top: 90%;
        left: 10%;
      }

      @include small-tablet {
        top: 85%;
      }

      @include mobile {
        left: 0;
      }
    }

    &-stairs {
      top: 50px;
      left: -400px;
      width: 500px;

      @include big-desktop {
        top: 90%;
        left: 10%;
      }

      @include small-tablet {
        top: 85%;
      }

      @include mobile {
        left: 0;
      }
    }

    &-front {
      top: 50px;
      left: -350px;

      @include big-desktop {
        top: 90%;
        left: 10%;
      }

      @include small-tablet {
        top: 85%;
        left: 24%;
      }

      @include mobile {
        left: 0;
      }
    }

    &-terrassa {
      top: 50px;
      left: -400px;
      width: 500px;

      @include big-desktop {
        top: 90%;
        left: 10%;
      }

      @include small-tablet {
        top: 85%;
      }

      @include mobile {
        left: 0;
      }
    }

    &-fundament {
      top: 50px;
      left: -530px;
      width: 600px;

      @include big-desktop {
        top: 90%;
        left: 10%;
      }

      @include small-tablet {
        top: 85%;
        left: 0;
      }
    }
  }

  &__item {
    align-items: center;
    position: relative;
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 400;
    font-size: 28px;
    line-height: 33px;
    text-align: right;
    color: #9B9B9B;
    transition: color 0.3s, opacity 0.7s;
    cursor: pointer;

    &:not(last-child) {
      padding-bottom: 36px;

      @include tablet {
        padding-bottom: 24px;
      }

      @include small-tablet {
        padding-bottom: 8px;
        font-size: 20px;
        line-height: 30px;
      }
    }

    &:hover {
      transition: color 0.3s;
      color: #000;
    }

    @include big-desktop {
      position: initial;
    }

    &.doors,
    &.net,
    &.frame,
    &.front,
    &.roof,
    &.stairs,
    &.terrassa,
    &.fundament {

      &::before {
        content: "";
        position: absolute;
        top: 33.7px;
        left: 0;
        border-radius: 50px;
        background-color: var(--color-primary);
        transition: opacity 0.7s;
        opacity: 0;

        @include big-desktop {
          opacity: 0 !important;
        }
      }

      &::after {
        content: "";
        position: absolute;
        top: 60%;
        right: 0;
        background-color: var(--color-primary);
        height: 2px;
        transition: opacity 0.7s;
        opacity: 0;

        @include big-desktop {
          opacity: 0 !important;
        }
      }
    }

    &--active {
      color: #000;
      transition: opacity 0.7s;
      opacity: 1;

      & .price-includes__text {
        transition: opacity 0.7s;
        opacity: 1;

        @include mobile {
          width: 100%;
        }
      }

      &.doors,
      &.net,
      &.frame,
      &.front,
      &.roof,
      &.stairs,
      &.terrassa,
      &.fundament {

        &::before {
          transition: opacity 0.7s;
          opacity: 1;
        }

        &::after {
          transition: opacity 0.7s;
          opacity: 1;
        }
      }

      &.roof {

        &::after {
          width: 910px;
        }

        &::before {
          width: 17px;
          height: 17px;
          left: -688px;
        }
      }

      &.front {
        &::after {
          width: 870px;

          @include big-desktop {
            opacity: 0 !important;
          }
        }

        &::before {
          width: 17px;
          height: 17px;
          left: -648px;
        }
      }

      &.stairs {
        &::after {
          width: 960px;

          @include big-desktop {
            width: 660px;
          }
        }

        &::before {
          width: 17px;
          height: 17px;
          left: -738px;

          @include big-desktop {
            left: -435px;
          }
        }
      }

      &.net {
        &::after {
          width: 1050px;

          @include big-desktop {
            width: 880px;
          }
        }

        &::before {
          width: 17px;
          height: 17px;
          left: -818px;

          @include big-desktop {
            left: -650px;
          }
        }
      }

      &.doors {
        &::after {
          width: 880px;

          @include big-desktop {
            width: 780px;
          }
        }

        &::before {
          width: 17px;
          height: 17px;
          left: -648px;

          @include big-desktop {
            left: -557px;
          }
        }
      }

      &.fundament {
        &::after {
          width: 870px;
        }

        &::before {
          width: 17px;
          height: 17px;
          left: -648px;
        }
      }

      &.terrassa {
        &::after {
          width: 770px;
        }

        &::before {
          width: 17px;
          height: 17px;
          left: -548px;
        }
      }

      &.frame {
        &::after {
          width: 760px;
        }

        &::before {
          width: 17px;
          height: 17px;
          left: -538px;
        }
      }
    }
  }
}
