.var-pay {
  @include flex-h-center;
  justify-content: space-around;

  @include mobile {
    flex-direction: column;
  }

  &__title {
    margin: 0 0 0 165px;
    padding: 82px 90px 170px 0px;
    width: 30%;
    position: relative;
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 400;
    font-size: 40px;
    line-height: 49px;
    color: var(--color-bblue);

    @include big-desktop {
      margin: 0 0 0 25px;
      padding: 82px 10px 170px 0;
    }

    @include small-tablet {
      font-size: 21px;
      line-height: 30px;
      padding: 60px 0px 90px 0;
      width: 20%;
    }

    @include mobile {
      padding: 60px 0px 10px 0;
      margin: 0px;
      text-align: center;
      width: 100%;
    }

    &::after {
      position: absolute;
      content: "";
      left: 2px;
      top: 220px;
      width: 96px;
      height: 11px;
      background-color: var(--color-primary);

      @include small-tablet {
        left: 0;
        top: 120px;
        width: 85px;
      }

      @include mobile {
        width: 0;
      }

    }
  }

  &__list {
    @include flex-h-center;
    justify-content: space-around;
    margin-top: 60px;
    width: 70%;

    @include small-tablet {
      margin-top: 10px;
      width: 80%;
    }

    @include mobile {
      width: 100%;
    }
  }

  &__item {
    width: 35%;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 29px;
    color: #000000;
    text-align: center;
    padding: 100px 25px 100px 25px;
    background-position: top;
    background-repeat: no-repeat;
    background-size: 96px 96px;

    @include small-tablet {
      font-size: 16px;
      line-height: 22px;
      padding: 100px 0px 18px 0px;
    }

    @include mobile {
      font-size: 13px;
    }


    &_house {
      background-image: url(../img/house-pay.png);

    }

    &_percent {

      background-image: url(../img/percent-pay.png);
    }

    &_strings {

      background-image: url(../img/strings-pay.png);
    }
  }
}
