.video-side {
  @include flex-all-center;

  @include mobile {
    flex-direction: column;
  }

  @include small-tablet {
    justify-content: left;
  }

  &__content-video {
    width: 100%;
    height: auto;
  }

  &__svg-play {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 120px;
    height: 120px;

  }

  &__content-text {
    max-width: 199px;
    position: absolute;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 500;
    font-size: 24px;
    line-height: 29px;
    color: var(--color-text);

    @include small-tablet {
      max-width: 100%;
      margin: 0;
      font-size: 21px;
      line-height: 27px;
    }
  }

  &__content {
    position: relative;
    width: 50%;
    margin: 100px 24px 90px 0px;

    @include small-tablet {
      width: 70%;
      margin: 50px 8px 50px 0;
    }

    @include mobile {
      width: 100%;
      margin-top: 20px;
      margin-right: 0;
    }
  }

  &__title {
    align-self: center;
    margin: 287px 0px 292px 66px;
    max-width: 24%;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 500;
    font-size: 60px;
    line-height: 73px;
    text-transform: uppercase;
    color: var(--color-primary);

    @include big-desktop {
      font-size: 34px;
      line-height: 46px;
      margin: 0;
    }

    @include small-tablet {
      font-size: 24px;
      line-height: 31px;
    }

    @include mobile {
      max-width: 100%;
      text-align: center;

    }
  }
}
