.equipment {
  padding: 30px;
  background-color: var(--color-elefant);
  padding-bottom: 90px;

  @include small-tablet {
    padding: 0px;
    padding-bottom: 30px;
  }

  &__container {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 10px;

    @include mobile {
      display: block;
    }
  }

  &__kind {
    grid-column: 5 span;
    margin-top: 53px;
    margin-left: 164px;

    @include big-desktop {
      grid-column: 4 span;
      margin-left: 0;
    }

    @include small-tablet {
      margin-top: 12px;
    }
  }

  &__accordion {
    transition: height 0.5s;
    padding: 42px 180px 51px 60px;
    background-color: var(--color-white);

    @include big-desktop {
      padding: 42px 55px 50px 8px;
    }

    @include small-tablet {
      padding: 42px 48px 50px 8px;
    }

    @include mobile {
      padding: 22px 48px 10px 8px;
    }
  }

  &__items {
    margin-bottom: 100px;

    @include small-tablet {
      margin-bottom: 70px;
    }

    @include mobile {
      margin-bottom: 20px;
      display: block;
      text-align: center;
    }
  }

  &__item {
    font-weight: 300;
    font-size: 24px;
    line-height: 29px;
    text-transform: uppercase;
    margin-bottom: 20px;

    &--checked {
      font-weight: 500;
    }

    @include small-tablet {
      font-size: 21px;
      line-height: 24px;
      margin-bottom: 10px;
    }

    @include mobile {
      display: inline;
      font-size: 14px;
      line-height: 22px;
    }
  }

  &__descr {
    grid-column: 7 span;
    margin-top: 57px;
    margin-left: 5px;

    @include big-desktop {
      grid-column: 8 span;
    }

    @include small-tablet {
      margin-top: 0;
    }
  }

  &__title {
    position: relative;
    width: 30%;
    font-family: "Montserrat";
    font-weight: normal;
    font-size: 40px;
    line-height: 49px;
    margin-bottom: 155px;
    color: #01041F;

    @include big-desktop {
      margin-bottom: 78px;
    }

    @include tablet {
      font-size: 30px;
      line-height: 40px;
      width: 100%;
    }

    @include small-tablet {
      font-size: 22px;
      line-height: 33px;
    }

    @include mobile {
      font-size: 14px;
      line-height: 22px;
      margin-bottom: 25px;
      text-align: center;
    }


    &::after {
      position: absolute;
      content: "";
      left: -3px;
      bottom: -50px;
      width: 96px;
      height: 11px;
      background-color: var(--color-primary);

      @include small-tablet {
        bottom: -20px;
        left: 0;
      }

      @include mobile {
        width: 62%;
        left: 50%;
        transform: translateX(-50%);
      }
    }
  }

  &__btn {
    padding: 23px 36px;

    @include tablet {
      font-size: 18px;
      padding: 13px 22px;
    }

    @include small-tablet {
      font-size: 13px;
      padding: 11px 19px;
    }

    @include mobile {
      display: block;
      margin: 0 auto;
      width: 60%;
    }
  }

}

.is-open {
  background-color: var(--color-indiblue);
  transition: height 0.5s;

  .accordion__title {
    transition: height 0.5s;
    color: var(--color-white);
  }
}


.accordion {

  &__control {
    position: relative;
  }

  &__icon {
    position: absolute;
    top: 7px;
    right: -133px;
    display: block;
    width: 34px;
    height: 32px;

    @include big-desktop {
      right: -50px;
    }

    @include small-tablet {
      right: -40px;
      width: 25px;
      height: 24px;
    }

    &::before {
      position: absolute;
      content: "";
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      width: 94%;
      height: 2px;
      background-color: var(--color-indiblue);


    }

    &::after {
      position: absolute;
      content: "";
      right: 50%;
      top: 0;
      transform: translateX(-50%);
      width: 2px;
      height: 100%;
      background-color: var(--color-indiblue);

    }
  }



  &__title {
    margin: 0;
    display: inline-block;
    width: 82%;
    margin-right: 100px;
    font-family: "Roboto";
    font-weight: 400;
    font-size: 28px;
    line-height: 33px;
    text-align: left;

    @include big-desktop {
      font-size: 25px;
    }

    @include tablet {
      font-size: 18px;
      width: 100%;
      margin-right: 0;
    }

    @include small-tablet {
      font-size: 15px;
      line-height: 26px;
    }

    @include mobile {
      font-size: 14px;
      line-height: 22px;
    }

    &--up {
      margin-top: 11px;
    }

    &--sub {
      margin: 0;
      text-align: left;
    }

    &--down {
      margin-top: 5px;

    }

    &--max-txt {
      text-align: left;
      text-transform: uppercase;
    }

    &--min-txt {
      text-transform: lowercase;
    }

    b {
      right: 0;
      position: absolute;

    }
  }

  &__about {
    margin-top: 80px;
    margin-left: 11px;

    @include small-tablet {
      margin-top: 20px;
    }

    span {
      text-transform: uppercase;
    }

    &--sub {
      span {
        margin-left: 11px;
        text-transform: uppercase;
      }
    }
  }

  &__content {
    transition: all 0.5s;
    font-family: "Roboto";
    font-weight: 400;
    font-size: 18px;
    line-height: 21px;
    color: var(--color-white);
    padding-left: 0px;

    @include big-desktop {
      font-size: 16px;
    }

    @include small-tablet {
      font-size: 14px;
    }
  }

  &__price {
    margin-left: 129px;
    font-family: "Roboto";
    font-weight: bold;
    font-size: 30px;
    line-height: 35px;
    text-align: center;
  }

  &__contant {
    span {
      text-transform: uppercase;
    }
  }

  &__control {
    border: none;
    padding: 0;
  }
}
