// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --font-family: "Montserrat", sans-serif;
  // --content-width: 1170px;
  --content-width: 1594px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --color-black: #000;
  --color-white: #fff;
  --color-bblue: #01041f;
  --color-primary: #ffcc00;
  --color-grey: #f2f1f1;
  --color-text: #1F1F1F;
  --color-scorpion: #5B5B5B;
  --color-indiblue: #00092B;
  --color-rgba: rgba(0, 9, 43, 0.02);
  --color-metal: #f6f6f6;
  --color-elefant: #F7F7F7;
  --color-blue: rgb(9, 144, 255);
}
