.layout-appart {
  padding: 70px 0 62px 0;
  background-color: var(--color-white);

  @include big-desktop {
    padding: 22px 0 22px 0;
  }

  @include mobile {
    padding-bottom: 86px;
  }
}

.layout-appart-variable {
  &__title {
    position: relative;
    margin: 0 auto;
    padding-top: 65px;
    max-width: 171px;
    font-family: "Montserrat";
    font-weight: normal;
    font-size: 40px;
    line-height: 49px;
    color: var(--color-text);
    background-color: var(--color-white);

    @include big-desktop {
      padding-top: 40px;
      max-width: 150px;
      font-size: 26px;
      line-height: 34px;
    }

    @include small-tablet {
      padding-top: 0;
      font-size: 22px;
      line-height: 28px;
    }


    &::after {
      position: absolute;
      content: "";
      left: -3px;
      bottom: -60px;
      width: 96px;
      height: 11px;
      background-color: var(--color-primary);

      @include big-desktop {
        left: 0;
        bottom: -30px;
        width: 70px;
        height: 7px;
      }

      @include small-tablet {
        bottom: -25px;
      }

    }
  }

  .swiper-slide {
    width: 498px;
    height: 324px;

    @include big-desktop {
      width: 260px;
      height: 180px;
    }

    @include small-tablet {
      width: 190px;
      height: 126px;
    }

  }
}
