.hero {
  margin-bottom: 153px;
  padding-top: 166px;
  color: var(--color-indiblue);
  background-image: url("../img/group-1.jpg");
  background-position: center -62px;
  background-size: auto;
  background-repeat: no-repeat;

  & .btn {
    @include tablet {
      font-size: 14px;
      line-height: 18px;
    }

    @include small-tablet {
      font-size: 12px;
      padding: 12px 9px;
    }

  }

  @include big-desktop {
    margin-bottom: 85px;
  }

  @include tablet {
    padding-top: 125px;
    margin-bottom: 10px;
  }

  @include mobile {
    padding-top: 80px;
  }


  &__container {
    padding-left: 144px;
    position: relative;

    @include big-desktop {
      padding-left: 135px;
    }

    @include tablet {
      padding-left: 80px;
    }

    @include small-tablet {
      padding-left: var(--container-offset);
    }

  }

  &__circular {
    width: 30em;
    height: 30em;
    font-family: "Roboto";
    font-style: normal;
    font-weight: 700;
    font-size: 5px;
    line-height: 19px;
    text-transform: uppercase;
    position: absolute;
    right: 220px;
    top: -200px;
    color: var(--color-primary);

    @include tablet {
      right: 33px;
      top: -145px;
    }

    @include mobile {
      width: 24em;
    }

  }

  &__title {

    margin-bottom: 82px;
    text-transform: uppercase;
    color: var(--color-indiblue);

    @include tablet {
      margin-bottom: 43px;
    }


    &--main {
      font-size: 48px;
      font-style: normal;
      line-height: 59px;
      color: var(--color-indiblue);
      font-weight: 400;
      max-width: 500px;

      @include big-desktop {
        font-size: 34px;
        max-width: 350px;
      }

      @include tablet {
        line-height: 34px;
        font-size: 29px;
        max-width: 310px;
      }

      @include small-tablet {
        font-size: 21px;
        line-height: 32px;
      }

      @include mobile {
        max-width: 200px;
        font-size: 16px;
      }

    }
  }

  &__btn {
    padding: 23px 47px;

    @include big-desktop {
      padding: 12px 24px;
      font-size: 18px;
    }
  }

  &__odds {
    margin-left: 30px;
    margin-top: 280px;

    @include big-desktop {
      margin-top: 185px;
    }

    @include tablet {
      margin-top: 60px;
      margin-left: 0;
    }

    @include mobile {
      margin-top: 380px;
    }
  }
}

.odds {
  display: grid;
  grid-template-columns: fit-content(230px) fit-content(430px);
  gap: 410px;

  @include big-desktop {
    gap: 140px;
  }

  @include tablet {
    display: block;
  }


  &__list {
    counter-reset: section;
    margin: 0;
    margin-top: 23px;
    font-family: "Roboto";
    font-size: 24px;
    line-height: 31px;
    color: var(--color-scorpion);
    min-width: 520px;

    @include big-desktop {
      margin-top: 64px;
      font-size: 18px;
    }

    @include tablet {
      margin-top: 104px;
      display: flex;
      font-size: 15px;
      min-width: 100px;
    }

    @include small-tablet {
      justify-content: space-around;
    }

    @include mobile {
      margin-top: 50px;
      flex-direction: column;
    }
  }

  &__title {
    margin: 0;
    position: relative;
    font-weight: 400;
    font-size: 40px;
    line-height: 49px;

    @include big-desktop {
      font-size: 36px;
    }

    @include tablet {
      line-height: 36px;
      font-size: 28px;
      width: 45%;
    }

    @include small-tablet {
      font-size: 27px;
    }

    @include mobile {
      font-size: 24px;
      line-height: 30px;
      width: 100%;
    }

    &::after {
      position: absolute;
      content: "";
      left: -6px;
      bottom: -35px;
      width: 96px;
      height: 11px;
      background-color: var(--color-primary);

      @include small-tablet {
        left: 0;
      }

    }
  }

  &__item {
    position: relative;

    @include tablet {
      margin-bottom: 0;
      width: 48%;
    }

    @include small-tablet {
      width: 40%;
    }

    @include mobile {
      width: 96%;
      margin-left: 23px;
    }

    &::before {
      font-family: "Roboto";
      font-weight: 300;
      font-size: 36px;
      line-height: 42px;
      text-align: center;
      color: var(--color-primary);
      position: absolute;
      counter-increment: section;
      content: "0"counter(section);
      margin-bottom: 18px;
      left: -78px;

      @include tablet {
        left: -45px;
        line-height: 34px;
        font-size: 28px;
      }

      @include small-tablet {
        left: -37px;
      }
    }

    &:not(:last-child) {
      margin-bottom: 60px;

      @include big-desktop {
        margin-bottom: 30px;
      }

    }
  }
}
