.header {
  padding-top: 70px;

  @include big-desktop {
    padding-top: 0px;
  }

  @include mobile {
    margin-bottom: 40px;
  }

  &__container {
    @include flex-v-center;
    justify-content: space-around;

    @include mobile {
      flex-wrap: wrap;
      align-content: flex-end;
    }
  }

  &__logo {
    order: 0;
    margin-left: 95px;

    @include big-desktop {
      margin-left: 35px;
    }

    @include small-tablet {
      margin-left: 0;
    }

    @include mobile {
      flex-grow: 0;

      width: 45%;
    }
  }

  &__nav {
    padding-top: 20px;
    order: 1;
    width: 585px;

    @include big-desktop {
      width: 545px;
    }

    @include tablet {
      order: 2;
      width: 70px;
    }

    @include mobile {
      order: 1;
      flex-grow: 1;
      width: 0%;
      margin-left: 30%;
    }
  }

  &__tel {
    order: 2;
    width: 20%;
    padding-top: 20px;

    @include big-desktop {
      width: 16%;
      padding-top: 16px;
    }

    @include tablet {
      order: 1;
      width: 25%;
    }

    @include mobile {
      order: 2;
      width: 80%;
      text-align: center;
    }

    &_link {
      font-family: "Roboto";
      font-weight: 500;
      font-size: 30px;
      line-height: 35px;
      text-align: right;
      color: var(--color-text);

      @include big-desktop {
        font-size: 20px;
      }

      @include small-tablet {
        font-size: 18px;
      }
    }
  }
}

.burger {
  position: relative;
  display: none;
  width: 30px;
  height: 20px;
  z-index: 11;

  &__line {
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    display: block;
    background-color: var(--color-bblue);
    height: 3px;
    width: 100%;
    transition: opacity 0.3s ease-in-out;
  }

  &::after,
  &::before {
    content: "";
    position: absolute;
    left: 0;
    height: 3px;
    width: 100%;
    background-color: var(--color-bblue);
    transition: transform 0.6s ease-in-out, top 0.3s ease-in-out, bottom 0.3s ease-in-out;
  }

  &::after {
    bottom: 0;
  }

  &::before {
    top: 0;
  }
}

@include tablet {
  .burger {
    display: block;

    &--active {
      & .burger__line {
        opacity: 0;
      }

      &::before {
        top: 50%;
        transform: rotate(-45deg);
      }

      &::after {
        bottom: auto;
        top: 50%;
        transform: rotate(225deg);
      }
    }
  }

  .header__nav {
    & .nav {
      position: fixed;
      right: 0;
      top: 0;
      padding: 30px;
      width: 100%;
      overflow-y: auto;
      height: 100vh;
      z-index: 10;
      background-color: rgba(9, 144, 255, 0.14);

      transform: translateX(100%);
      transition: transform 0.7s ease-in-out;

      &--active {
        transform: none;
      }

      &__list {
        flex-direction: column;
        align-items: center;
      }

      &__item {
        &:not(:last-child) {
          margin-right: 0;
          margin-bottom: 70px;
        }
      }

      &__link {
        font-size: 25px;

        @include mobile {
          font-size: 22px;
        }
      }
    }
  }

}
