figure {
  margin: 0;
  padding: 0;
}

.gellary {
  background-color: var(--color-rgba);
  position: relative;
  padding-bottom: 225px;

  &__container {
    position: relative;
    left: 161px;
    display: grid;
    grid-template-columns: 36em 30em 1fr;
    grid-template-rows: 0.7fr 1fr;
    grid-template-areas:
      "pic1 pic2 pic3"
      "pic4 pic2 .";
    width: 113em;
    row-gap: 39px;
    column-gap: 49px;
    padding-top: 110px;

    @media (max-width: (1600px)) {
      left: 0;
      column-gap: 12px;
    }

    @include big-desktop {
      left: 0;
      width: 64em;
      row-gap: 4px;
      padding-top: 75px;
      grid-template-columns: 1fr 1fr 1fr;
      grid-template-areas:
        "pic1 pic2 pic3"
        "pic4 pic2 .";
    }

    @include tablet {
      padding-top: 45px;
      grid-template-areas:
        "pic1 pic2"
        "pic4 pic3";
      width: auto;
      grid-template-columns: 1fr 1fr;
    }

    @include mobile {
      grid-template-areas:
        "pic1"
        "pic2"
        "pic4"
        "pic3";
      row-gap: 53px;
      grid-template-columns: 1fr;

    }
  }


  &__caption {
    padding-top: 10px;
    width: 90px;
    position: absolute;
    font-size: 18px;
    line-height: 22px;

    span {
      font-size: 13px;
      letter-spacing: 2px;
      text-transform: uppercase;
    }

    &__descr {
      display: flex;
      flex-direction: row-reverse;
    }
  }

  &__circular {
    width: 43em;
    height: 43em;
    font-family: "Roboto";
    font-style: normal;
    font-weight: 700;
    font-size: 6px;
    line-height: 19px;
    text-transform: uppercase;
    position: absolute;
    bottom: 250px;
    left: 283px;
    color: var(--color-text);

    @include big-desktop {
      font-size: 5px;
      line-height: 12px;
      left: 760px;
    }

    @include tablet {
      left: 530px;
      bottom: 196px;
    }

  }

  .pic1 {
    position: relative;
    grid-area: pic1;

    @include mobile {
      grid-row: 1;

    }
  }

  .pic2 {
    grid-area: pic2;
    grid-row: span 2;

    @include big-desktop {
      margin-left: 0;
      justify-self: start;

    }

    @include tablet {
      grid-row: span 1;
    }

    @include mobile {
      grid-row: 2;
      justify-self: center;
    }
  }

  .pic3 {
    grid-area: pic3;
    justify-self: center;
  }

  .pic4 {
    margin-top: 6px;
    margin-left: 50px;
    justify-self: end;
    align-self: start;
    grid-area: pic4;

    @include big-desktop {
      margin-left: 0;
      justify-self: start;
    }

    @include tablet {
      margin-top: -158px;
    }

    @include mobile {
      margin-top: 0px;
      justify-self: center;
    }
  }
}

.descr {
  position: absolute;
  bottom: 93px;
  right: 309px;
  display: flex;
  flex-direction: column;

  @include big-desktop {
    bottom: 34px;
    right: 193px;
  }

  @include tablet {
    right: 140px;
  }

  @include small-tablet {
    right: 50px;
  }

  @include mobile {
    right: 0px;
    bottom: -1px;
  }

  &::after {
    position: absolute;
    content: "";
    left: 211px;
    top: -53px;
    width: 596px;
    height: 2px;
    background-color: var(--color-primary);

    @include mobile {
      top: -35px;
      left: 54px;
    }
  }

  &__text {
    margin: 0;
    display: inline-block;
    margin-bottom: 54px;
    width: 498px;
    font-family: "Roboto";
    font-weight: 400;
    font-size: 18px;
    line-height: 21px;
    text-align: right;

    @include tablet {
      text-align: center;
    }

    @include mobile {
      width: 99%;
      font-size: 15px;
      margin-bottom: 43px;
    }
  }

  &__btn {
    align-self: flex-end;
    padding: 23px 45px;

    @include tablet {
      align-self: center;
    }
  }
}
