.order {
  background-color: var(--color-grey);

  &__container {
    display: flex;
    justify-content: space-around;
    padding-top: 119px;

    @include big-desktop {
      padding-top: 60px;
    }

    @include mobile {
      padding-top: 20px;
      flex-direction: column;
      margin-top: 10px;
    }
  }

}

.data-order {
  width: 45%;
  margin-bottom: 75px;

  @include small-tablet {
    margin-bottom: 25px;
  }

  @include mobile {
    margin-top: 80px;
    width: 95%;
  }
}

.text-order {
  margin-top: -5px;
  margin-left: 80px;
  width: 30%;

  @include small-tablet {
    margin-left: 0;
  }

  @include mobile {
    width: 100%;

  }

  &__title {
    margin: 0;
    font-family: "Montserrat";
    font-weight: 500;
    font-size: 40px;
    line-height: 49px;
    color: var(--var-bblue);

    @include small-tablet {
      font-size: 34px;
      line-height: 40px;
    }

    @include mobile {
      font-size: 24px;
      line-height: 30px;
      text-align: center;
    }
  }

  &__descr {
    margin: 0;
    margin-top: 18px;
    position: relative;
    font-family: "Montserrat";
    font-weight: 400;
    font-size: 18px;
    line-height: 22px;

    &::after {
      position: absolute;
      content: "";
      left: -1px;
      bottom: -57px;
      width: 96px;
      height: 11px;
      background-color: var(--color-primary);

      @include small-tablet {
        bottom: -40px;
      }

      @include mobile {
        width: 100%;
      }

    }

    @include mobile {
      text-align: center;
    }
  }
}

.form {
  display: flex;
  flex-direction: column;
  align-items: flex-start;


  &__title {
    margin: 0;
    margin-bottom: 24px;
    width: 100%;
    font-weight: 700;
    font-size: 28px;
    line-height: 150%;
    text-align: center;
    color: var(--color-text);
  }



  &__checkbox {
    margin-bottom: 22px;
  }

  &__btn {
    padding: 19px 34px;

    @include small-tablet {

      font-size: 14px;
      padding: 12px 19px;
    }
  }
}
