.square {

  &__kind {
    margin: 0;
    padding: 0;
    font-weight: 300;
    font-size: 24px;
    line-height: 29px;
    text-align: center;
    margin-top: 10px;
    margin-left: 100px;

    @include small-tablet {
      margin-left: 30px;
      margin-top: 0;
    }

    @include mobile {
      margin-left: 0;
      font-size: 14px;
    }
  }

  &__list {
    font-family: "Montserrat";
    font-weight: 300;
    font-size: 24px;
    line-height: 29px;
    text-align: center;
    text-transform: uppercase;
    margin-top: 50px;
    margin-bottom: 39px;
    margin-left: 425px;

    @include big-desktop {
      margin-left: 125px;
    }

    @include small-tablet {
      margin-bottom: 16px;
    }

    @include tablet {
      margin-left: 20px;
    }

    @include mobile {
      font-size: 14px;
      line-height: 19px;
      margin: 0;
      text-align: center;
    }
  }

  &__item {
    display: inline;
    text-align: center;

    @include mobile {
      font-size: 14px;
      vertical-align: middle;
    }

    span {
      text-transform: none;
    }

    &:not(:last-child) {
      margin-right: 78px;

      @include small-tablet {
        margin-right: 20px;
      }

      @include mobile {
        margin-right: 5px;
      }
    }
  }

  &__checked {
    font-weight: 500;
  }



  &__container {
    background-color: var(--color-metal);
    display: flex;
    flex-direction: row;
    align-items: center;

    @include small-tablet {
      justify-content: space-around;
    }


    &--range {
      margin-top: 36px;

      @include small-tablet {
        padding: 10px 0 10px;
        margin-top: 0;
      }
    }
  }


}

.square-keys {

  .square__list {
    margin-left: 0;
    padding: 46px 0 44px 0;
  }

  .square__container {
    display: block;
  }

  .square__item {
    text-transform: none;
    text-align: center;
  }
}
