.logo {
  margin-right: 110px;

  @include big-desktop {
    margin-right: 0;
  }

  @include small-tablet {
    align-self: flex-end;
    margin-bottom: 0;
  }

  @include mobile {
    align-self: center;
  }
}

.footer {
  background-color: var(--color-bblue);

  &__container {
    @include flex-v-center;
    justify-content: space-between;
    padding-bottom: 100px;

    @include tablet {
      padding-bottom: 30px;
    }

    @include mobile {
      flex-direction: column;
    }
  }

  &__list {
    margin-left: 90px;

    @include tablet {
      margin-left: 0;
    }

  }


  &__nav {
    margin-top: 72px;

    @include small-tablet {
      margin-top: 30px;
    }

    @include mobile {
      margin-bottom: 20px;
    }
  }

  &__copy {
    color: var(--color-white);
    margin-left: 162px;
    font-family: Roboto;
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;

    @include tablet {
      margin-left: 0;
    }

    @include small-tablet {
      font-size: 12px;
    }

    @include mobile {
      margin-left: 24px;
    }

  }

  &__logo {
    display: block;
    margin-left: 162px;
    margin-bottom: 44px;

    @include tablet {
      margin-left: 0;
      margin-bottom: 40px;
    }

    @include small-tablet {
      margin-bottom: -35px;
    }

    @include mobile {
      text-align: center;
    }

    img {
      @include small-tablet {
        max-width: 76%;
      }

      @include mobile {
        max-width: 99%;
      }
    }
  }

  &__tel {
    margin-top: -26px;
    margin-right: 105px;

    @include big-desktop {
      margin-right: 75px;
    }

    @include tablet {
      margin-right: 0;
      margin-bottom: 15px;
    }

    @include small-tablet {
      align-self: flex-end;
      margin-bottom: 0;
    }

    @include mobile {
      margin-top: 0;
      align-self: center;
    }
  }
}

.tel {
  &__link {
    font-family: Roboto;
    font-style: normal;
    font-weight: 500;
    font-size: 30px;
    line-height: 35px;
    text-align: right;
    color: var(--color-white);

    @include tablet {

      font-size: 16px;
    }
  }
}
