.project {
  margin-bottom: 98px;

  @include tablet {
    margin-bottom: 38px;
  }
}

.slide {
  margin: 0 auto;
  padding: 0px;
  max-width: 1595px;
  @include flex-all-sb;
  margin-bottom: 62px;

  @include tablet {
    margin-bottom: 22px;
  }

  @include mobile {
    flex-direction: column;
  }

  &__text {
    align-self: flex-start;
    width: 31.3%;
    margin-top: 50px;

    @include big-desktop {
      margin-top: 0;
    }

    @include tablet {
      margin-bottom: 22px;
    }

    @include small-tablet {
      margin-bottom: 0;
    }

    @include mobile {
      width: 100%;
      align-self: center;
    }
  }

}

.slider {
  &__image {
    position: relative;
    padding: 5px 5px 0px 5px;
  }
}

.swiper-slide-container {
  padding: 0;
}

.project {
  margin-top: 62px;

  @include small-tablet {
    margin-top: 20px;
  }

  &__title {
    position: relative;
    margin: 0;
    text-align: left;
    font-family: "Montserrat";
    font-weight: 500;
    font-size: 60px;
    line-height: 73px;
    color: var(--color-primary);

    @include big-desktop {
      font-size: 46px;
      line-height: 56px;
    }

    @include tablet {
      font-size: 39px;
      line-height: 48px;
    }

    @include small-tablet {
      font-size: 25px;
      line-height: 20px;
    }

    @include mobile {
      display: inline;
      text-align: center;
    }


    &::after {
      content: "";
      left: 0;
      top: 240%;
      width: 135%;
      position: absolute;
      height: 3px;
      background-color: var(--color-primary);

      @include tablet {
        top: 230%;
      }

      @include small-tablet {
        top: 260%;
      }
    }

    &-sub {
      margin: 0;
      margin-bottom: 100px;
      text-align: left;
      font-family: "Montserrat";
      font-weight: 500;
      font-size: 60px;
      line-height: 73px;
      color: var(--color-primary);

      @include big-desktop {
        font-size: 46px;
        line-height: 55px;
        margin-bottom: 55px;
      }

      @include tablet {
        font-size: 36px;
        line-height: 48px;
        margin-bottom: 40px;
      }

      @include small-tablet {
        margin-bottom: 24px;
        font-size: 25px;
        line-height: 36px;
      }

      @include mobile {
        display: inline;
      }
    }
  }

  &__price {
    margin: 0;
    max-width: 350px;
    text-align: left;
    font-weight: 400;
    font-size: 36px;
    line-height: 44px;
    margin-bottom: 104px;

    @include big-desktop {
      margin-bottom: 44px;
    }

    @include tablet {
      font-size: 24px;
      line-height: 35px;
      margin-bottom: 30px;
    }

    @include small-tablet {
      margin-bottom: 48px;
      font-size: 20px;
      line-height: 28px;
    }

    @include mobile {
      max-width: 100%;
      text-align: right;
      margin-bottom: 5px;
      font-size: 14px;
      line-height: 23px;
    }

    span {
      font-weight: bold;
      font-size: 40px;
      line-height: 49px;

      @include tablet {
        font-size: 28px;
        line-height: 36px;
      }

      @include small-tablet {
        font-size: 20px;
        line-height: 28px;
      }
    }
  }

  &__btn {
    padding: 23px 36px;

    @include tablet {
      font-size: 16px;
    }

    @include small-tablet {
      font-size: 14px;
      line-height: 22px;
      padding: 9px 12px;
    }
  }
}

.swiper-container {
  width: 100%;
  height: 300px;
}

.swiper-slide {

  &__text {
    padding: 0;
    padding-top: 8px;
    margin-top: 22px;
    margin-left: 19px;
    background-color: var(--color-white);
    font-family: "Montserrat";
    font-weight: 400;
    font-size: 24px;
    line-height: 29px;

    @include tablet {
      margin-top: 0;
    }

    @include small-tablet {
      text-align: center;
      margin-left: 2px;
    }
  }

  &__name {
    padding: 0;
    margin: 0;

    @include tablet {
      font-size: 16px;
      line-height: 28px;
    }

    @include small-tablet {
      font-size: 15px;
    }

    @include mobile {
      font-size: 12px;
    }
  }
}

.gallery-top {
  height: 80%;
  width: 100%;

  &__img {
    max-width: 85%;

    @include big-desktop {
      max-height: 399px;
    }

    @include tablet {
      max-height: 316px;
    }

    @include small-tablet {
      max-height: 240px;
    }

    @include mobile {
      max-width: none;
      width: 100%;
    }
  }
}

.gallery-thumbs {
  height: 20%;
  box-sizing: border-box;
  padding: 5px 0;
}

.gallery-thumbs .swiper-slide {
  width: 20%;
  height: 100%;
}

.gallery-thumbs .swiper-slide-active {

  &::before {
    content: "";
    position: absolute;
    -webkit-box-shadow: inset 0px 0px 0px 5px var(--color-primary);
    -moz-box-shadow: inset 0px 0px 0px 5px var(--color-primary);
    box-shadow: inset 0px 0px 0px 5px var(--color-primary);
    top: 5px;
    left: 5px;
    bottom: 93px;
    right: -5px;
    z-index: 1;

    @include tablet {
      bottom: 69px;
    }
  }

  .swiper-slide__name,
  span {
    font-weight: 500;
  }
}

