.promo-services {
  background-image: url(../img/promo-services-img.png);
  background-position: center;
  background-repeat: no-repeat;
  background-size: 101% 103%;
  display: flex;
  justify-content: center;
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));

  @include mobile {
    flex-direction: column;
    background-size: 500%;
  }

  &__title {
    margin: 172px 18px 130px 300px;
    font-family: 'Proxima Nova';
    font-style: normal;
    font-weight: 400;
    font-size: 60px;
    line-height: 73px;
    text-align: right;
    text-transform: uppercase;
    color: var(--color-primary);

    @include big-desktop {
      font-size: 34px;
      line-height: 46px;
    }

    @include tablet {
      font-size: 24px;
      line-height: 34px;
      margin: 60px 0px 20px 300px;
    }

    @include mobile {
      margin: 10px 0px 20px 0px;
    }
  }

  &__text {
    margin: 172px 400px 132px 70px;
    max-width: 483px;
    font-family: 'Proxima Nova';
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 29px;
    color: #fff;

    @include tablet {
      margin: 60px 315px 20px 30px;
    }

    @include small-tablet {
      font-size: 15px;
      line-height: 23px;
      max-width: 330px;
      margin-left: 5px;
    }

    @include mobile {
      margin-right: 15px;
      margin-top: 10px;
    }

    &_bottom {
      font-family: 'Proxima Nova';
      font-style: normal;
      font-weight: 400;
      font-size: 18px;
      line-height: 22px;
      color: var(--color-primary);
      white-space: nowrap;

      @include small-tablet {
        font-size: 12px;
      }
    }
  }
}
