.nav {

  &__item {
    &:not(:last-child) {
      margin-right: 38px;
    }

    &--footer {
      &:not(:last-child) {
        padding-bottom: 18px;
      }

      margin-right: 0px;
      text-align: left;

      &.nav__item {
        @include small-tablet {
          margin-right: 0;
        }

        @include mobile {
          text-align: center;
        }
      }
    }

  }

  &__list {
    @include flex-v-center;
  }

  &__link {
    font-family: "Roboto";
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 19px;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    transition: color 0.3s;
    color: var(--color-text);
    display: inline-block;
    text-decoration: none;
    cursor: pointer;

    &_active {
      color: var(--color-blue);
    }

    &:after {
      background-color: var(--color-blue);
      display: block;
      content: "";
      height: 3px;
      width: 0%;
      -webkit-transition: width .3s ease-in-out;
      -moz--transition: width .3s ease-in-out;
      transition: width .3s ease-in-out;
    }

    &:hover:after,
    &:focus:after {
      width: 100%;
    }

    &:hover {
      transition: color 0.3s;
      color: var(--color-primary);
    }

    &--footer {
      color: var(--color-white);

      font-weight: 400;
    }

  }

}
