.our-address {
  @include flex-h-center;
  padding-left: max(15px, calc((100% - 1272px) / 2));
  line-height: 49px;
  color: var(--color-bblue);
  margin-bottom: 80px;

  @include tablet {
    justify-content: space-between;
  }

  @include small-tablet {
    flex-direction: column;
    padding-left: 0;
    margin-bottom: 20px;
  }

  @include mobile {
    margin-bottom: 0;
  }

  &__text {
    flex-shrink: 0;
    width: 430px;
    align-self: flex-start;
    margin-right: 55px;

    @include tablet {
      margin-right: 0;
      width: 330px;
    }

    @include small-tablet {
      width: 100%;
    }
  }

  &__street {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 30px;
    line-height: 37px;
    color: var(--color-text);

    @include small-tablet {
      margin: 0;
      font-size: 25px;
      line-height: 35px;
    }

    @include mobile {
      font-size: 18px;
      max-width: 370px;
    }
  }

  &__work-hours {
    padding-top: 50px;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 500;
    font-size: 30px;
    line-height: 37px;
    color: var(--color-text);

    @include small-tablet {
      padding-top: 0;
    }

    @include mobile {
      margin: 0;
      margin-top: -34px;
      font-size: 22px;
      line-height: 30px;
      text-align: right;
    }
  }

  &__times {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 30px;
    line-height: 37px;
    color: var(--color-text);

    @include mobile {
      font-size: 26px;
      text-align: right;
    }
  }

  &__title {
    position: relative;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 40px;

    @include tablet {
      font-size: 29px;
      line-height: 36px;
    }

    @include mobile {
      margin: 0;
    }

    &::after {
      position: absolute;
      content: "";
      left: 0px;
      top: 220px;
      width: 96px;
      height: 11px;
      background-color: var(--color-primary);
    }
  }

}

.map {
  width: calc(100% - 430px - 55px);
  height: 556px;
  background-color: #000;

  @include tablet {
    width: calc(100% - 330px - 55px);
    height: 450px;
  }

  @include small-tablet {
    width: 100%;
  }
}

[class*="copyrights-pane"] {
  display: none !important;
}
