.form-field {
  display: flex;
  margin-bottom: 30px;

  @include small-tablet {
    width: 100%;
  }

  &__button {
    margin-top: 19px;
    margin-left: 18px;

    @include mobile {
      align-self: center;
    }
  }

  &__caption {
    margin-bottom: 7px;
    font-size: 0px;
    // font-size: 18px;
    line-height: 150%;
    color: var(--color-gray);
    font-family: "Montserrat";
    font-weight: normal;
    line-height: 22px;
    color: var(--color-text);
  }

  &__input:focus {
    outline: 3px solid var(--color-primary);
  }

  &__input {
    margin-left: 20px;
    border: none;
    background-color: var(--color-white);
    padding: 34px 23px 34px;
    height: 44px;
    font-family: "Montserrat";
    font-weight: normal;
    font-size: 18px;
    line-height: 22px;
    color: var(--color-text);


    &--name {
      width: 336px;

      @include small-tablet {
        width: 100%;
      }
    }

    &--tel {
      width: 498px;

      @include big-desktop {
        width: 336px;
      }

      @include small-tablet {
        width: 100%;
      }
    }

    &::placeholder {
      color: var(--color-text);
    }

  }

}
